body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.gallery-image {
  position: absolute;
  overflow: hidden;
  cursor: pointer;
}

.gallery-image:hover img {
  transform: scale(1.04);
  transition: transform 0.5s 0.3s;
}

.gallery-image img {
  cursor: pointer;
  transition: transform 0.3s;
}

.image-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.9);
}

.image-overlay img {
  display: block;
  max-width: calc(100% - 40px);
  max-height: calc(100% - 20px);
  margin: auto;
}

.image-overlay header {
  display: flex;
  justify-content: center;
  padding-left: 60px;
  padding-right: 60px;
}

.image-overlay section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100% - 60px);
}

.image-overlay header span {
  font-size: 18px;
  display: inline-block;
  margin: 12px auto;
  color: #efefef;
  width: 33.333333%;
  text-align: center;
}

.image-overlay header span:first-child {
  text-align: left;
}

.image-overlay header span:last-child {
  text-align: right;
  background-image: url("./assets/multiply.svg");
  background-repeat: no-repeat;
  background-position: right center;
  background-size: 12px;
  cursor: pointer;
}

.image-overlay header span a {
  margin: 0 auto;
  color: #f5f5f5;
  text-decoration: none;
}

.image-overlay header span a:hover {
  color: #f1f1f1;
}


.image-overlay section .arrow {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 50px;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  background-position: center;
  background-size: 30px;
  background-repeat: no-repeat;
}

.image-overlay section .arrow:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

.image-overlay section .arrow-left {
  left: 0;
  background-image: url("./assets/back.svg");
}

.image-overlay section .arrow-right {
  right: 0;
  background-image: url("./assets/next.svg");
}